.tab__content {
  width: 100%;
  height: 90%; }

.tab__content__item {
  opacity: 0;
  width: 0;
  height: 0;
  display: none; }

.tab__content__selected {
  opacity: 1;
  width: 100%;
  height: 100%;
  display: block; }

.tab__content__top {
  position: absolute;
  top: 10%; }

.item__selected {
  background: #252526; }

.tab__list {
  display: grid;
  width: 100%;
  height: 10%;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  cursor: pointer; }

.tab__list a {
  color: white; }

[class*="tab__item"] {
  display: flex;
  align-items: center;
  justify-content: center; }

.tab__item__top {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: relative;
  overflow: hidden; }
  .tab__item__top:before {
    content: "";
    position: absolute;
    z-index: -1;
    bottom: 0;
    left: 50%;
    right: 50%;
    background: white;
    height: 4px;
    border-radius: 10px;
    -webkit-transition-property: left right;
    transition-property: left right;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out; }
  .tab__item__top:hover:before {
    left: 0;
    right: 0; }

.tab__item__bottom {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: relative;
  overflow: hidden; }
  .tab__item__bottom:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    right: 50%;
    background: white;
    height: 4px;
    border-radius: 10px;
    -webkit-transition-property: left right;
    transition-property: left right;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out; }
  .tab__item__bottom:hover:before {
    left: 0;
    right: 0; }
