[class*="btn"] {
  background-color: #252526;
  border: 1px;
  font-size: 1em;
  color: white; }

[class*="btn"]:focus {
  outline: none; }

[class*="btn"]:hover {
  background: #606060; }

[class*="btn"]:active {
  background: #1e1e1e; }

.btn__rd {
  border-radius: 15px; }

.btn__sm {
  font-size: 0.9em;
  padding: 0 0.5em;
  min-width: 2em;
  min-height: 2em; }

.btn__md {
  padding: 0 0.7em;
  min-width: 2.3em;
  min-height: 2.3em; }

.btn__lg {
  font-size: 1.1em;
  padding: 0 1em;
  min-width: 2.5em;
  min-height: 2.5em; }

.btn__full {
  width: 100%;
  height: 100%;
  margin: 0; }
